import React, { useState, useEffect, useRef } from 'react';
import {
	useParams,
	useHistory,
} from 'react-router-dom';
import Helmet from 'react-helmet';
import { ReactSEOMetaTags } from 'react-seo-meta-tags';
import { useTranslation } from 'react-i18next';
import { isSafari, isMobile } from 'react-device-detect';
// TODO: handle Scroll Smooth
// import * as Scroll from 'react-scroll';
import {
	Link, Element, Events, animateScroll as scroll, scrollSpy, scroller,
} from 'react-scroll';

import './MainLayout.scss';
// import { Dispatch } from 'redux';
// import { RootState } from 'MyTypes';
// import { connect } from 'react-redux';
import Header from '../Header/Header';
import SideBar from '../SideBar/SideBar';
import Splash from '../../sections/Splash/Splash';
import Landing from '../../sections/Landing/Landing';
import Video from '../../sections/Video/Video';
import Journey from '../../sections/Journey/Journey';
import Exhibition from '../../sections/Exhibition/Exhibition';
import Tour from '../../sections/Tour/Tour';
import Gallery from '../../sections/Gallery/Gallery';
import Artisan from '../../sections/Artisan/Artisan';
import SubscribePopup from '../../sections/SubscribePopup/SubscribePopup';
import Footer from '../../sections/Footer/Footer';

// Lang
import i18n from '../../i18n/i18n';

// Helper
import { trackSection } from '../../helper/helper';

// interface Props {
// 	children?: ReactNode;
// }

export const MainLayout = () => {
	const { lang: paramLang } = useParams();
	const { t } = useTranslation();
	const history = useHistory();

	const changeLang = (lang: string) => {
		history.replace(`/${lang}`);
		i18n.switchLanguage(lang);
	};

	useEffect(() => {
		// const query = new URLSearchParams(window.location.search);
		// const paramLang = query.get('lang');
		const defaultLang = 'en';
		const lang = localStorage.getItem('i18nextLng');
		if (paramLang) {
			changeLang(paramLang);
		} else if (lang) {
			changeLang(lang || defaultLang);
		} else {
			changeLang(defaultLang);
		}
	}, [paramLang]);

	const [pageProgess, setPageProgress] = useState<any>({
		pageReady: '',
		splash: '',
		landing: '',
		video: '',
		afterVideo: '',
		journey: '',
		exibition: '',
		tour: '',
		gallery: '',
		artisan: '',
		footer: '',
	});

	// Subscribe Popup (For section Artisan)
	const [subsPopupOpen, setSubsPopupOpen] = useState<boolean>(false);
	const openSubsPopup = () => {
		setSubsPopupOpen(true);
	};
	const closeSubsPopup = () => {
		setSubsPopupOpen(false);
	};

	const sectionRefs = useRef<any>([]);

	const updatePageProgess = (prgroess: any) => {
		setPageProgress(prgroess);
	};

	const scrollToSec = (key: string) => {
		// console.log('scrollToRef', ref);
		const windowWidth = window.innerWidth
			|| document.documentElement.clientWidth
			|| document.body.clientWidth;

		const offset = (windowWidth > 767) ? 45 : -55;
		scroller.scrollTo(`anchor-${key}`, {
			duration: 1000,
			delay: 50,
			smooth: true,
			offset,
		});
		// return ;
		// const targetRef = sectionRefs?.current[key];
		// if (targetRef) {
		// 	const tarTop = targetRef.getBoundingClientRect().top;
		// 	if (isMobile) {
		// 		window.scrollTo(0, tarTop);
		// 		setTimeout(() => {
		// 			window.scrollTo(0, tarTop + 5);
		// 		}, 150);
		// 	} else {
		// 		window.scrollTo({
		// 			top: tarTop,
		// 			behavior: 'smooth',
		// 		});
		// 	}
		// }
	};

	useEffect(() => {
		window.dispatchEvent(new CustomEvent('scroll'));
	}, [pageProgess]);


	useEffect(() => {
		trackSection('splash');
	}, []);

	// eslint-disable-next-line max-len
	const inverseColor = pageProgess.artisan || ((pageProgess.video || pageProgess.pageReady) && !pageProgess.journey && pageProgess.afterVideo);

	return (
		<>
			<ReactSEOMetaTags
				render={(el: React.ReactNode) => <Helmet>{el}</Helmet>}
				website={{
					title: t('siteMetadata.title'),
					description: t('siteMetadata.description'),
					language: t('siteMetadata.language'),
				}}
			/>
			<div className={`main-layout ${pageProgess.pageReady ? 'page-ready' : ''} ${pageProgess.splash ? 'splash-finished' : ''} ${pageProgess.video ? 'in-video' : ''} ${pageProgess.afterVideo ? 'after-video' : ''}  ${inverseColor ? 'color-inversed' : ''}  ${pageProgess.tour ? 'after-exhibition' : ''}`}>
				{/* <div className="sec-info-ref">
				{JSON.stringify(pageProgess)}
			</div> */}
				<div className="main-layout__body">
					<h1 className="a11y-hidden">NWD50</h1>
					<SideBar
						inverseColor={inverseColor}
						pageProgess={pageProgess}
						updatePageProgess={updatePageProgess}
						scrollToSec={scrollToSec}
						changeLang={changeLang}
					/>
					<main className="main-layout__main">
						{/* {children} */}
						<Splash
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Landing
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Video
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Element name="anchor-journey">
							<div
								className="main-layout__section-spacer"
								ref={(ref) => {
									sectionRefs.current.journey = ref;
								}}
							/>
						</Element>
						<Journey
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Element name="anchor-exhibition">
							<div
								className="main-layout__section-spacer"
								ref={(ref) => {
									sectionRefs.current.exhibition = ref;
								}}
							/>
						</Element>
						<Exhibition
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Element name="anchor-tour">
							<div
								className="main-layout__section-spacer"
								ref={(ref) => {
									sectionRefs.current.tour = ref;
								}}
							/>
						</Element>
						<Tour
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						<Element name="anchor-gallery">
							<div
								className="main-layout__section-spacer"
								ref={(ref) => {
									sectionRefs.current.gallery = ref;
								}}
							/>
						</Element>
						<Gallery
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
						{/* <Element name="anchor-artisan">
						<div
							className="main-layout__section-spacer"
							ref={(ref) => {
								sectionRefs.current.artisan = ref;
							}}
						/>
					</Element>
					<Artisan
						pageProgess={pageProgess}
						updatePageProgess={updatePageProgess}
						onClickSubsBtn={openSubsPopup}
					/> */}
						{/* <SubscribePopup
						open={subsPopupOpen}
						onClosePopup={closeSubsPopup}
					/> */}
						<div
						// className="main-layout__section-spacer"
							ref={(ref) => {
								sectionRefs.current.footer = ref;
							}}
						/>
						<Footer
							pageProgess={pageProgess}
							updatePageProgess={updatePageProgess}
						/>
					</main>
				</div>
				{/* </Scrollbar> */}
			</div>
		</>
	);
};


export default MainLayout;

